<template>
  <div class="fill-height">
    <app-bar title="Pedidos de CARP físico">
      <template v-slot:append>
        <v-btn icon to="/utilidades" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <async-container :loading="loading">
      <v-container>
        <v-card :dark="$store.state.dark">
          <v-data-table
            :headers="headers"
            :items="items"
            sort-by="exercicio"
            sort-desc
            no-data-text="Nenhum cartão protocolado"
          >
            <template v-slot:item.data_registro="{item}">{{parseDate(item.data_registro)}}</template>
            <template v-slot:item.entrega="{item}">
              <span v-if="item.entrega === 0">Sede de Campo Grande</span>
              <span v-else-if="item.entrega === 1">Subsede de Dourados</span>
              <span v-else-if="item.entrega === 2">Subsede de Três Lagoas</span>
              <span v-else-if="item.entrega === 3">Envio por correio</span>
              <span v-else>Desconhecido...</span>
            </template>
            <template v-slot:item.status="{item}">
              <v-chip v-if="item.status === 0" color="error" label small>Em confecção</v-chip>
              <v-chip v-else-if="item.status === 1" color="primary" label small>Pronto</v-chip>
              <v-chip v-else-if="item.status === 2" color="error" label small>Indeferido</v-chip>
              <v-chip v-else-if="item.status === 3" color="success" label small>Enviado</v-chip>
              <v-chip v-else-if="item.status === 4" color="success" label small>Retirado</v-chip>
              <v-chip v-else>Desconhecido...</v-chip>
            </template>
          </v-data-table>
        </v-card>
        <v-btn
          block
          color="primary"
          class="mt-3 mb-7"
          @click="exibirFormPedirCarp"
          :loading="loadingAddCarp"
        >
          <v-icon class="mr-1">mdi-plus-circle</v-icon>Solicitar
        </v-btn>
      </v-container>
    </async-container>
    <v-dialog v-model="dialogPedirCarp" width="420" max-width="94%" persistent>
      <v-card>
        <v-card-title>
          Solicitar cartão
          <v-spacer></v-spacer>
          <v-icon @click="dialogPedirCarp = false">mdi-close</v-icon>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="py-8">
          <v-select
            label="Local de retirada"
            :items="iptLocalRetiradaItemsSmart"
            v-model="iptLocalRetirada"
            hide-details
            outlined
          ></v-select>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="primary" :loading="loadingAddCarp" @click="registrarCarp">Confirmar</v-btn>
          <v-btn color="primary" :disabled="loadingAddCarp" @click="dialogPedirCarp = false" outlined>Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AsyncContainer from '@/components/AsyncContainer';
import CrecimsWebClient from '@/http/CrecimsWebClient';
import AppBar from '@/components/AppBar';
import moment from "moment";
export default {
  name: 'CrecimsCarp',
  components: {AsyncContainer, AppBar},
  data: () => ({
    loading: true,
    headers: [
      {value: 'exercicio', text: 'Exercício'},
      {value: 'data_registro', text: 'Data do pedido'},
      {value: 'entrega', text: 'Local de retirada'},
      {value: 'status', text: 'Situação'},
    ],
    items: [],
    webclient: new CrecimsWebClient(),
    dialogPedirCarp: false,
    iptLocalRetirada: 0,
    iptLocalRetiradaItems: [
      {value: 0, text: 'Sede de Campo Grande'},
      {value: 1, text: 'Subsede de Dourados'},
      {value: 2, text: 'Subsede de Três Lagoas'},
      {value: 3, text: 'Enviar por correio'},
    ],
    loadingAddCarp: false,
    aptoCorreio: false,
  }),
  methods: {
    async loadData() {
      try {
        this.items = await this.webclient.getCarps(this.$store.getters['session/payload'].cpf);
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
        await this.$router.push('/utilidades');
      } finally {
        this.loading = false;
      }
    },
    parseDate: (datetime) => moment(datetime).format('DD/MM/YYYY HH:mm'),
    async exibirFormPedirCarp() {
      this.loadingAddCarp = true;
      try {
        this.aptoCorreio = await this.webclient.validarPodePedirCarp(this.$store.getters['session/payload'].cpf);
        this.dialogPedirCarp = true;
      } finally {
        this.loadingAddCarp = false;
      }
    },
    async registrarCarp() {
      this.loadingAddCarp = true;
      try {
        await this.webclient.insertCarp(this.$store.getters['session/payload'].cpf, this.iptLocalRetirada);
        await this.loadData();
        this.dialogPedirCarp = false;
      } finally {
        this.loadingAddCarp = false;
      }
    },
  },
  computed: {
    iptLocalRetiradaItemsSmart() {
      return this.aptoCorreio ? this.iptLocalRetiradaItems : this.iptLocalRetiradaItems.filter(i => i.value !== 3);
    },
  },
  created() {
    this.loadData();
  },
}
</script>

<style scoped>

</style>