import { httpSnackbar, httpSilent } from '@/plugins/axios'

export default class CrecimsWebClient {
  http = httpSnackbar();

  /**
   * Construtor
   * @param {boolean} silent
   */
  constructor(silent = false) {
    if (silent) this.http = httpSilent();
  }

  async getCarps(cpf) {
    const {data} = await this.http.get('crecims/carp?cpf=' + cpf);
    return data;
  }

  async validarPodePedirCarp(cpf) {
    const formData = new FormData();
    formData.append('cpf', cpf);
    const {data} = await this.http.patch('crecims/carp', formData);
    return data;
  }

  async insertCarp(cpf, local) {
    const formData = new FormData();
    formData.append('cpf', cpf);
    formData.append('local', local.toString());
    await this.http.post('crecims/carp', formData);
  }
}